import React from "react";
import {NavLink} from "react-router-dom";
import "./index.css";
const Menu=()=>{
    return(
        <>
        <div className="container-fluid nav_bg">
            <div className="row">
                <div className="col-10 mx-auto">

               
<nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <NavLink className="navbar-brand" to="/">Ujju Services</NavLink>
    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink activeClassName="menu_active" className="nav-link" exact aria-current="page" to="/">Home</NavLink>
        </li>
       
        <li className="nav-item">
          <NavLink activeClassName="menu_active"className="nav-link " to="/service"  role="button"  aria-expanded="false">
            Services
          </NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="menu_active"className="nav-link" to="/about"  aria-disabled="true">About Us</NavLink>
        </li>
        <li className="nav-item">
          <NavLink activeClassName="menu_active"className="nav-link" to="/contact"  aria-disabled="true">Contact Us</NavLink>
        </li>
      </ul>
      
    </div>
  </div>
</nav>
 </div>
            </div>
        </div>
       
        </>
    )
}
export default Menu;